.clock.is-hidden {
  opacity: 0;
}

.clock__items {
  max-width: max-content;
  margin: 0 auto 50px;
  font-size: 48px;
  display: flex;
}

.clock__item, .timezone {
  min-width: 60px;
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
  padding-bottom: 15px;
  position: relative;
}

.clock__item:nth-child(1) {
  margin-left: 60px;
}

.clock__item:before {
  content: attr(data-title);
  font-size: 14px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.clock__item:not(:nth-child(3)):after {
  content: ":";
  position: absolute;
  right: -15px;
}

.buttons__wrapper {
  max-width: max-content;
  margin: 0 auto;
}

.show-clock__btn.is-hidden, .hide-clock__btn.is-hidden, .change-format__btn.is-hidden {
  display: none;
}

.change-format__btn {
  min-width: 115px;
}

.timezone.is-hidden {
  opacity: 0;
}

button[type="button"] {
  color: #fff;
  cursor: pointer;
  height: 36px;
  background-color: #f04747;
  border: none;
  border-radius: 5px;
  margin-right: 8px;
  padding: 8px 16px;
  font-size: 16px;
  transition: background-color .3s;
}

button[type="button"].show-clock__btn {
  background-color: #43b581;
}

button[type="button"].show-clock__btn:hover {
  background-color: #3fb072;
}

